import Parser from "rss-parser";
import translate from "@iamtraction/google-translate";
import { parse } from "node-html-parser";
import htmlToMarkdown from "html-to-md";

export function estimateReadTime(content: string) {
  if (!content) return `1 min read`;
  const words = content.trim().split(/\s+/).length;
  const minutes = Math.max(1, Math.ceil(words / 200));
  return `${minutes} min read`;
}

const RSS_FEED_URL = "https://blog.google/products/gemini/rss/";

export type FeedItem = {
  title: string;
  link: string;
  pubDate: string;
  guid: string;
  content: string; // Markdown
  contentSnippet?: string;
  categories?: string[];
};

// ğŸ’¡ HTML ë³¸ë¬¸ì„ ê°€ì ¸ì™€ Markdownìœ¼ë¡œ ë³€í™˜ + ë…¸ì´ì¦ˆ ì œê±°
async function fetchFullArticleAsMarkdown(link: string): Promise<string> {
  try {
    const res = await fetch(link, { next: { revalidate: 3600 } });
    if (!res.ok) throw new Error(`Failed to fetch article: ${link}`);

    const html = await res.text();
    const root = parse(html);

    // Google Blog ë³¸ë¬¸ì€ <article> íƒœê·¸ ì•ˆì— ìˆìŒ
    const article = root.querySelector("article");
    if (!article) return "";

    // --- [1] ë¶ˆí•„ìš”í•œ ìš”ì†Œ ì œê±° ---
    article.querySelectorAll("nav, header, footer").forEach((el) => el.remove());
    article.querySelectorAll("aside, .share, .share-links, .breadcrumbs").forEach((el) => el.remove());
    article.querySelectorAll("a").forEach((a) => {
      const text = a.textContent?.trim() || "";
      // â€œShareâ€, â€œCopy linkâ€ ê°™ì€ ë§í¬ëŠ” ì œê±°
      if (/^(Share|Copy link|Twitter|Facebook|LinkedIn|Mail)$/i.test(text)) a.remove();
    });

    // --- [2] HTMLì„ Markdownìœ¼ë¡œ ë³€í™˜ ---
    let markdown = htmlToMarkdown(article.innerHTML);

    // --- [3] í›„ì²˜ë¦¬(clean-up) ---
    markdown = markdown
      .replace(/\n{3,}/g, "\n\n") // ì¤„ë°”ê¿ˆ ì •ë¦¬
      .replace(/Breadcrumb.*?\n/gi, "") // Breadcrumb ì œê±°
      .replace(/POSTED IN:.*$/gim, "") // í•˜ë‹¨ ì¹´í…Œê³ ë¦¬ ì œê±°
      .replace(/\[Share\]\(.*?\)/g, "") // ê³µìœ  ë§í¬ ì œê±°
      .replace(/Copy link/g, "")
      .replace(/(\*\*?\s*)+/g, "**"); // ì´ìƒí•œ êµµê²Œ ì²˜ë¦¬ ì •ë¦¬

    markdown = markdown.trim();

    return markdown;
  } catch (error) {
    console.error("Error fetching or converting article:", error);
    return "";
  }
}

export async function getGeminiProductFeed(): Promise<FeedItem[]> {
  try {
    const response = await fetch(RSS_FEED_URL, { next: { revalidate: 3600 } });
    if (!response.ok) throw new Error(`Failed to fetch RSS feed: ${response.statusText}`);

    const xmlText = await response.text();
    const parser = new Parser<Record<string, never>, FeedItem>({
      customFields: {
        item: [["content:encoded", "content"]],
      },
    });

    const feed = await parser.parseString(xmlText);
    const plainItems: FeedItem[] = feed.items.map((item) => ({
      title: item.title,
      link: item.link,
      pubDate: item.pubDate,
      guid: item.guid,
      content: item.content || "",
      contentSnippet: item.contentSnippet,
      categories: item.categories,
    }));

    // --- ì „ì²´ ë³¸ë¬¸(Markdown ë³€í™˜ í¬í•¨) ---
    const itemsWithFullMarkdown = await Promise.all(
      plainItems.map(async (item) => {
        const markdown = await fetchFullArticleAsMarkdown(item.link);
        return { ...item, content: markdown || item.content };
      })
    );

    // --- ë²ˆì—­ (ì œëª© + ìš”ì•½ë§Œ) ---
    const translatedItems = await Promise.all(
      itemsWithFullMarkdown.map(async (item) => {
        try {
          const [translatedTitle, translatedSnippet] = await Promise.all([
            translate(item.title, { from: "en", to: "ko" }),
            translate(item.contentSnippet || "", { from: "en", to: "ko" }),
          ]);
          return {
            ...item,
            title: translatedTitle.text,
            contentSnippet: translatedSnippet.text,
          };
        } catch {
          return item;
        }
      })
    );

    return translatedItems;
  } catch (error) {
    console.error("Error fetching or parsing Gemini RSS feed:", error);
    return [];
  }
}
